import { CButton, CFormLabel } from "@coreui/react";
import { useEffect, useState } from "react";
import {
  CRow,
  CCol,
  CInputGroup,
  CInputGroupText,
  CFormSelect,
  CCard,
  CCardHeader,
  CCardBody,
} from "@coreui/react";
import tr7TradingBot from "../../Model/tr7-trading-bot";

const DEFAULT_SYMBOL = "BTCUSDT";

const RSIConfigure = () => {
  const [curSymbol, $curSymbol] = useState(DEFAULT_SYMBOL);
  const [symbolList, $symbolList] = useState([]);
  // load current RSI configuration
  useEffect(() => {
    (async function fetchRSIConfigure() {
      return await tr7TradingBot.getRSIConfig();
    })().then((res) => {
      const sortSymbols = res.symbols.sort();
      $symbolList(sortSymbols);
      if (sortSymbols.indexOf(DEFAULT_SYMBOL) === -1) {
        $curSymbol(sortSymbols[0]);
      }
    });
  }, []);

  function symbolChangeHandler(e) {
    $curSymbol(e.target.value);
  }
  function rsiConfigClickHandler() {}

  return (
    <CCard className="mb-4">
      <CCardHeader>RSI Config</CCardHeader>
      <CCardBody>
        <CRow className="d-flex d-flex-row justify-content-begin">
          <CCol xs={7} lg={4}>
            <CInputGroup className="mb-2">
              <CInputGroupText
                component="label"
                htmlFor="inputSymbol"
                className=""
              >
                Symbol
              </CInputGroupText>
              <CFormSelect
                id="inputSymbol"
                onChange={symbolChangeHandler}
                value={curSymbol}
              >
                {symbolList.map((symbol, index) => {
                  return <option key={index}>{symbol}</option>;
                })}
              </CFormSelect>
            </CInputGroup>
          </CCol>
          <CCol xs={7} lg={6}>
            range goes here
          </CCol>
          <CCol>
            <CButton onClick={rsiConfigClickHandler}>Submit</CButton>
          </CCol>
        </CRow>
      </CCardBody>
    </CCard>
  );
};
const RSIState = ({ stateHandler }) => {
  const [running, $running] = useState();
  useEffect(() => {
    // load state from backend server
    //
    stateHandler(running);
  });
  function startStopClickHandler() {
    const state = !running;
    // toggle state
    $running(state);
    // submit to server
    // set state on higher component
    stateHandler(state);
  }

  return (
    <CCard className="mb-4">
      <CCardHeader>RSI State</CCardHeader>
      <CCardBody>
        <CRow className="d-flex d-flex-row justify-content-around">
          <CCol xs={10} lg={3}>
            <CFormLabel>
              RSI State: {running ? "running" : "stopped"}
            </CFormLabel>
          </CCol>
          <CCol xs={10} lg={3}>
            <CButton
              color={running ? "danger" : "warning"}
              size="lg"
              onClick={startStopClickHandler}
            >
              {running ? "Stop" : "Start"}
            </CButton>
          </CCol>
        </CRow>
      </CCardBody>
    </CCard>
  );
};

const RSISymbolView = () => {
  const [curSymbol, $curSymbol] = useState(DEFAULT_SYMBOL);
  const [symbolList, $symbolList] = useState([]);

  useEffect(() => {
    (async function fetchSymbolList() {
      return await tr7TradingBot.getSymbolList();
    })().then((res) => {
      const sortSymbols = res.symbols.sort();
      $symbolList(sortSymbols);
      if (sortSymbols.indexOf(DEFAULT_SYMBOL) === -1) {
        $curSymbol(sortSymbols[0]);
      }
    });
  }, []);

  function symbolChangeHandler(e) {
    $curSymbol(e.target.value);
  }

  return (
    <CCard className="mb-4">
      <CCardHeader>RSI View</CCardHeader>
      <CCardBody>
        <CRow className="d-flex d-flex-row justify-content-around">
          <CCol xs={10} lg={3}>
            <CInputGroup className="mb-2">
              <CInputGroupText
                component="label"
                htmlFor="inputSymbol"
                className=""
              >
                Symbol
              </CInputGroupText>
              <CFormSelect
                id="inputSymbol"
                onChange={symbolChangeHandler}
                value={curSymbol}
              >
                {symbolList.map((symbol, index) => {
                  return <option key={index}>{symbol}</option>;
                })}
              </CFormSelect>
            </CInputGroup>
          </CCol>
          <CCol xs={10} lg={3}>
            <div>timeframe list</div>
          </CCol>
        </CRow>
        <CRow>RSI Chart here</CRow>
      </CCardBody>
    </CCard>
  );
};

const RSIAnalysic = () => {
  const [running, $running] = useState();
  function onStateChange(state) {
    $running(state);
  }
  return (
    <>
      <h1>RSI Analysis</h1>
      <RSIConfigure />
      <RSIState stateHandler={onStateChange} />
      {running && <RSISymbolView />}
    </>
  );
};

export default RSIAnalysic;
